    .mov_ul_title {
        display: grid;
        max-width: calc(100vw - 48px);
        grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
        grid-gap: 16px;
        margin-top: 0;
        margin-bottom: 0;
        padding-top: 20px;
        padding-bottom: 20px;
        list-style: none;
        margin-left: auto;
        margin-right: auto;
    } 
    
    
    .cast_img {
        display: block;
        width: 300px;
        height: 450px;
    }
    
    .mov_text {
        font-size: 16px;
    }
    
    .cast_text_add {
        margin-left: 20px;
        font-style: oblique;
    }
  